import { tasks } from './taskList'
let groupTasks = {
  priority1: [],
  priority2: [],
  priority3: [],
  priority4: []
}
const clearGroupTasks = () => {
  groupTasks = {
    priority1: [],
    priority2: [],
    priority3: [],
    priority4: []
  }
}
const group = (x) => {
  console.log('entered')
  clearGroupTasks()
  tasks.forEach((task) => {
    console.log(tasks)
    console.log(task)
    console.log(groupTasks[`${'priority' + task[0][x]}`])
    console.log(task[0][x])
    groupTasks[
      `${'priority' + task[0][x]}`.replaceAll(' ', '').toLowerCase()
    ].push(task)
  })
  console.log(groupTasks)
}
export { group, groupTasks }
